@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

img {
  display: block;
  height: auto;
  object-fit: contain;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  font-size: 20px;
  line-height: 20px;
  padding: 20px 20px 15px 20px;
}

@media (max-width: 767px) {
  .main-header {
    position: relative;
    padding: 0;
  }
  .main-header::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: #d9d9d9;
  }
}
@media (min-width: 768px) {
  .main-header {
    padding: 17px 30px 15px 30px;
  }
}
@media (min-width: 1280px) {
  .main-header {
    max-width: 1220px;
    padding: 50px 0 35px 0;
    margin: 0 auto;
  }

  .main-header--index {
    max-width: 100%;
    background: linear-gradient(to right, #ffffff 0%, #ffffff 50%, #69b738d9 50%, #69b738d9 100%);
  }
  .main-header--index .navigation__logo {
    width: 232px;
  }
  .main-header--index .navigation__link {
    color: #ffffff;
  }
  .main-header--index .navigation__link:focus, .main-header--index .navigation__link:focus-visible {
    outline: none;
  }
  .main-header--index .navigation__link[href]:focus-visible {
    outline: 2px solid #ffffff;
    outline-offset: 2px;
  }
  .main-header--index .navigation__item--current {
    position: relative;
  }
  .main-header--index .navigation__item--current::after {
    background: #ffffff;
  }
}
.logo {
  font-family: "Oswald", "Arial", sans-serif;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  user-select: none;
  display: flex;
  align-items: center;
}

.logo__link:hover {
  opacity: 0.8;
}
.logo__link:active {
  opacity: 0.3;
}
.logo__link:focus, .logo__link:focus-visible {
  outline: none;
}
.logo__link:focus-visible {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}

.navigation {
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.navigation__logo[href]:hover {
  opacity: 0.8;
}
.navigation__logo[href]:active {
  opacity: 0.6;
}
.navigation__logo:focus, .navigation__logo:focus-visible {
  outline: none;
}
.navigation__logo[href]:focus-visible {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}

.navigation__logo-image {
  width: 190px;
  height: 38px;
}

.navigation__toggle {
  display: block;
  position: relative;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.navigation__toggle:hover::before, .navigation__toggle:hover::after {
  opacity: 0.6;
}
.navigation__toggle:active::before, .navigation__toggle:active::after {
  opacity: 0.3;
}

.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.navigation--opened .navigation__toggle {
  top: 0;
  right: 0;
}
.navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  z-index: 5;
}

.navigation__item {
  padding-top: 22px;
  padding-bottom: 24px;
  flex-shrink: 0;
}

.navigation__link {
  font-family: "Oswald", "Arial", sans-serif;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  user-select: none;
  padding: 15px 50px;
}
.navigation__link[href]:hover {
  opacity: 0.6;
}
.navigation__link[href]:active {
  opacity: 0.3;
}
.navigation__link:focus, .navigation__link:focus-visible {
  outline: none;
}
.navigation__link[href]:focus-visible {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}

@media (max-width: 767px) {
  .navigation {
    padding-top: 13px;
    padding-bottom: 15px;
  }
  .navigation .logo {
    margin-left: 20px;
  }
  .navigation .navigation__toggle {
    margin-right: 21px;
  }

  .navigation--opened .navigation__list {
    width: 100%;
    position: absolute;
    top: 65px;
    left: 0;
    margin: 0 auto;
    flex-direction: column;
    text-align: center;
    background-color: #ffffff;
  }

  .navigation--closed .navigation__list {
    display: none;
  }

  .navigation__item {
    border-bottom: 1px solid #e6e6e6;
  }

  .navigation__item:first-child {
    border-top: 1px solid #e6e6e6;
  }

  .navigation--nojs {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-bottom: 0;
  }
  .navigation--nojs .navigation {
    width: 100%;
  }
  .navigation--nojs .navigation__logo-image {
    flex-grow: 1;
    margin: 0 auto;
  }
  .navigation--nojs .navigation__toggle {
    opacity: 0;
    pointer-events: none;
  }
  .navigation--nojs .navigation__list {
    display: block;
    width: 100%;
    position: static;
    text-align: center;
    padding-top: 10px;
  }
}
@media (min-width: 768px) {
  .navigation__logo-image {
    width: 175px;
    height: 50px;
  }

  .navigation__toggle {
    display: none;
  }

  .navigation__list {
    width: 440px;
    font-size: 18px;
    padding-top: 14px;
    padding-bottom: 12px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .navigation__item {
    max-width: 170px;
    padding-top: 10px;
    padding-bottom: 12px;
  }
  .navigation__item--current {
    position: relative;
  }
  .navigation__item--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #68b738;
  }

  .navigation__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 33px;
  }

  .navigation__link {
    padding: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
@media (min-width: 1280px) {
  .navigation__logo-image {
    width: 202px;
    height: 59px;
  }

  .navigation__list {
    width: 477px;
    font-size: 20px;
    padding-top: 16px;
    padding-bottom: 13px;
    flex-wrap: nowrap;
  }

  .navigation__item:not(:last-child) {
    margin-right: 33px;
  }
}
.page-main {
  flex-grow: 1;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  background-color: #ffffff;
}

.program-form {
  width: 100%;
  color: #ffffff;
  background: linear-gradient(to top, #ffffff 0%, #ffffff 180px, #69b738d9 180px, #69b738d9 100%);
  line-height: 14px;
}

.program-form__container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  min-width: 320px;
  padding: 0 20px;
  padding-top: 27px;
  margin-bottom: 20px;
}

.program-form__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  color: #000000;
  margin: 0;
  line-height: 36px;
  color: #ffffff;
  margin-bottom: 25px;
  text-align: center;
}

.program-form__slogan {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 29px;
  text-align: center;
}

.program-form__image {
  margin: 0 auto;
  margin-bottom: 3px;
}

.program-form__link {
  width: 100%;
  display: inline-block;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #69b738;
  text-decoration: none;
  border: none;
  cursor: pointer;
  user-select: none;
  padding: 10px 44px;
  padding: 10px 44px;
}
.program-form__link:hover {
  background-color: #5eaa2f;
  transition: all 0.5s ease;
}
.program-form__link:active span {
  opacity: 0.3;
  transition: all 0.5s ease;
}
.program-form__link:focus, .program-form__link:focus-visible {
  outline: none;
}
.program-form__link:focus-visible {
  outline: 2px solid #68b738;
  outline-offset: 2px;
}

@media (max-width: 767px) {
