    background-image: image-set(url("../img/index-background-phone.webp") type("image/webp") 1x, url("../img/index-background-phone@2x.webp") type("image/webp") 2x, url("../img/index-background-phone.jpg") type("image/jpg") 1x, url("../img/index-background-phone@2x.jpg") type("image/jpg") 2x);
    background-repeat: no-repeat;
    background-size: auto 302px;
    background-position: top;
    position: relative;
  }

  .program-form__title {
    min-height: 72px;
  }
}
@media (min-width: 768px) {
