  }
  .program-form__container::before {
    content: "";
    background-image: image-set(url("../img/index-background-desktop.webp") type("image/webp") 1x, url("../img/index-background-desktop@2x.webp") type("image/webp") 2x, url("../img/index-background-desktop.jpg") type("image/jpg") 1x, url("../img/index-background-desktop@2x.jpg") type("image/jpg") 2x);
    width: 720px;
    height: 695px;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -159px;
    margin-right: -110px;
  }

  .program-form__title {
    margin-left: 0;
  }

  .program-form__slogan {
    margin-left: 0;
